import React from 'react';
import { useComplexLanguageMethod } from '../../../contexts/languageContext';

const Intro = ({ header, subHeader }) => {
  const { t } = useComplexLanguageMethod();
  return (
    <>
      <h1>{t(header)}</h1>
      <div className='intro-section'>
        <h2>{t(subHeader)}</h2>
        <section>
          <p className='online'>
            <big>Online</big>
            <br />
            <small>
              <small>Life connected in tech</small>
            </small>
          </p>
          <p className='offline'>
            <big>Offline</big>
            <br />
            <small>Normal biologic life</small>
          </p>
        </section>
      </div>
    </>
  );
};

export default Intro;
